@import "../../../../Global/ScssPartials/systemColorScheme";
.ConferGeneral {
  border-radius: 4px;
  cursor: pointer;
  user-select: false;
  border: 1px solid var(--primary-color);
  transition: all 0.3s ease !important;
  padding: 0.3em;
  @media screen and (min-width: 769px) {
    margin:.1em;
  }
  margin-top: 0.5em;
  user-select: none;
  padding-block: 0;

}
.ConferGeneral_select {
  border-radius: 4px;
  cursor: pointer;
  user-select: false;
  border: 5px solid var(--primary-color);
  transition: all 0.3s ease !important;
  padding: 0.3em;
  @media screen and (min-width: 769px) {
    margin:.1em;
  }
  margin-top: 0.5em;
  user-select: none;
  padding-block: 0;

}
.ConferName {
  text-align: right;
  width: 70%;
  align-self: flex-end;
  margin-right: 0.3em;
}
.confersContainer {
  @media screen and (min-width: 769px) {
    display: grid !important;
    grid-template-columns: repeat(2, 1fr) !important;
    gap: "1em" !important;
  }
}
